.i {
    height: 100vh;
    display: flex;
}

.i-left {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.i-right {
    flex: 1;
    position: relative;
}
.i-left-wrapper {
    padding: 50px;
    height: 35%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.i-name {
    font-size: 60px;
}

.i-title {
    height: 50px;
    width: 100%;
    overflow: hidden;
}

.i-title-wrapper {
    height: 100%;
    animation: move 6s ease-in-out infinite alternate;
}

@keyframes move {
    50% {
        transform: translateY(-50px);
    }
    100% {
        transform: translateY(-100px);
    }
}

.i-title-item {
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: #1463f3;
    align-items: center;
}

.i-desc {
    font-weight: 300;
}

.i-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
}

.i-bg {
    clip-path: polygon(30% 0, 100% 0%, 100% 100%, 30% 100%, 0% 50%);
    background-color: #1463f3;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
}

.i-scroll {
    width: 30px;
    height: 30px;
    position: relative;
    top: 18%;
}


@media screen and (max-width: 480px) {
    .i {
        flex-direction: column;
    }

    .i-left-wrapper {
        height: 50%;
        padding: 10px;
        align-items: center;
        text-align: center;
    }

    .i-name {
        font-size: 50px;
    }

    .i-title-item {
        text-align: center;
    }

    .i-img {
        object-fit: contain;
    }
}

@media screen and (min-width: 481px) and (max-width: 768px) {
    .i-left-wrapper {
        height: 45%;
    }

    .i-name {
        font-size: 40px;
    }

    .i-scroll {
        top: 13%;
    }
}

@media screen and (min-width: 769px) and (max-width: 1280px) {
    .i-name {
        font-size: 50px;
    }
}

@media screen and (min-width: 1281px) {
    .i-img {
        object-fit: contain;
    }
}